<template>
  <div id="guess">
    <h1>抽认卡</h1>
    <div class="box">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span style="position: relative; left: 22px">题号:{{ temporaryQuestion.id }}</span>
          <el-button @click="getQuestion" style="float: right; padding: 3px 0" type="text" round>下一题</el-button>
        </div>
        <div class="main">
          <div>{{ temporaryQuestion.main }}</div>
          <p>选择你的答案</p>
          <span class="anser" v-for="(item, index) in temporaryAnser" :key="index"
            ><a @click="istry(item, index)" href="#">{{ item }}</a>
            <!-- v-if="!isClicked[index]"  是否让点击的错误答案消失 -->
          </span>
          <p v-if="isattantion" style="color: green">回答正确</p>
          <p v-if="iserr" style="color: red">回答错误</p>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'guess',
  data() {
    return {
      questionBank: [
        {
          id: 0,
          main: 'Ambiguous',
          reaAnswer: '含糊不清的'
        },
        {
          id: 1,
          main: 'Culminate',
          reaAnswer: '达到顶点'
        },
        {
          id: 2,
          main: 'Deteriorate',
          reaAnswer: '恶化'
        },
        {
          id: 3,
          main: 'Elusive',
          reaAnswer: '难以捉摸的'
        },
        {
          id: 4,
          main: 'Facilitate',
          reaAnswer: '促进'
        },
        {
          id: 5,
          main: 'Hierarchy',
          reaAnswer: '等级制度'
        },
        {
          id: 6,
          main: 'Impartial',
          reaAnswer: '公正的'
        },
        {
          id: 7,
          main: 'Jeopardize',
          reaAnswer: '危及'
        },
        {
          id: 8,
          main: 'Lucrative',
          reaAnswer: '有利可图的'
        },
        {
          id: 9,
          main: 'Notorious',
          reaAnswer: '臭名昭著的'
        },
        {
          id: 10,
          main: 'Overtake',
          reaAnswer: '超过'
        },
        {
          id: 11,
          main: 'Proliferate',
          reaAnswer: '增殖'
        },
        {
          id: 12,
          main: 'Quota',
          reaAnswer: '配额'
        },
        {
          id: 13,
          main: 'Resilient',
          reaAnswer: '有弹性的'
        },
        {
          id: 14,
          main: 'Substantiate',
          reaAnswer: '证实'
        },
        {
          id: 15,
          main: 'Tactile',
          reaAnswer: '触觉的'
        },
        {
          id: 16,
          main: 'Unanimous',
          reaAnswer: '一致的'
        },
        {
          id: 17,
          main: 'Viable',
          reaAnswer: '可行的'
        },
        {
          id: 18,
          main: 'Wary',
          reaAnswer: '谨慎的'
        },
        {
          id: 19,
          main: 'Zeal',
          reaAnswer: '热情'
        }
      ],
      temporaryQuestionBank: [],
      temporaryQuestion: {},
      temporaryAnser: [],
      isattantion: false,
      iserr: false,
      isClicked: []
    }
  },
  mounted() {
    this.getQuestion()
  },
  methods: {
    getQuestion() {
      if (this.temporaryAnser !== []) {
        this.temporaryQuestionBank = []
        this.temporaryQuestion = {}
        this.temporaryAnser = []
        this.isattantion = false
        this.isClicked = []
      }
      const oneId = Math.floor(Math.random() * 19)
      this.temporaryQuestionBank = JSON.parse(JSON.stringify(this.questionBank))
      this.temporaryQuestion = this.temporaryQuestionBank[oneId]
      this.getErrQuestion()
      this.temporaryAnser.push(this.temporaryQuestion.reaAnswer)
      const shuffledArray = this.shuffleArray(this.temporaryAnser)
      // console.log(this.temporaryQuestion)
      // console.log(shuffledArray)
    },
    getErrQuestion() {
      let trueId = this.temporaryQuestion.id
      this.temporaryQuestionBank.splice(trueId, 1)
      for (let i = 0; i < 3; i++) {
        let a = Math.floor(Math.random() * 19)
        this.temporaryAnser.push(this.temporaryQuestionBank[a].reaAnswer)
      }
    },
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
        ;[array[i], array[j]] = [array[j], array[i]]
      }
      return array
    },
    istry(item, index) {
      // console.log(item)
      if (item === this.temporaryQuestion.reaAnswer) {
        this.isattantion = true
        this.iserr = false
      } else {
        // this.$set(this.isClicked, index, true) // 将对应索引的链接设为已点击状态
        this.isattantion = false
        this.iserr = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
#guess {
  width: 400px;
  height: 370px;
  padding: 50px 50px;
  border-radius: 15px;
  background-color: seagreen;

  h1 {
    text-align: center;
  }
}

a {
  text-decoration: none;
  color: #666875;
}
.box {
  display: flex;
  .box-card {
    width: 500px;
    margin: auto;

    .anser {
      margin: 10px;
      padding: 5px;
      // border: 1px solid;
      border-radius: 5px;
      background-color: #c5c7cb;
      line-height: 50px;
    }
  }
}
</style>
